import {Component, OnInit} from "@angular/core";
import {InventorySelectionEvent} from "@component/inventory/inventory-node";
import {AlertType} from "@component/validation/alert-type";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {ConfigureStretchedClusterService} from "@generated/configure-stretched-cluster-service";
import {DiskManagementService} from "@generated/disk-management-service";
import {FilterContextKey} from "@generated/filter-context-key";
import {InventoryEntryData} from "@generated/inventory-entry-data";
import {VsanClaimDisksData} from "@generated/vsan-claim-disks-data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {WitnessCandidateInventoryService} from "@generated/witness-candidate-inventory-service";
import {WitnessHostValidationResult} from "@generated/witness-host-validation-result";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {WitnessHostValidation, WitnessValidationService} from "@service/witness-validation.service";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "witness-host-page.html",
   styleUrls: ["witness-host-page.scss"],
})
@WizardPage(
      VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.title"),
)
export class WitnessHostPageComponent implements OnInit, OnValidate {

   inventoryRoot: ManagedObject[];
   filterContext: Map<FilterContextKey, ManagedObject>;
   hostValidation: WitnessHostValidation;

   @InputProperty()
   vsanConfigSpec: VsanConfigSpec;

   @InputProperty()
   private pageControls: PageControls;

   @InputProperty()
   @OutputProperty()
   clusterRef: ManagedObject;

   @InputProperty(InputProperty.required(false))
   currentWitnessHostRef: ManagedObject;

   @OutputProperty(OutputProperty.ifNull(VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.witnessHost.validation.noSelection")))
   witnessHost: ManagedObject;

   @OutputProperty()
   witnessHostName: string;

   @OutputProperty()
   hasDiskGroupsOnWitness: boolean = null;

   /*
    The witnessHostDiskMapping property is set in ClaimDisksWitnessHostPage.
    It is conditional based on whether the selected host has disk groups.
    Since the property is required for 'startingTaskProperty' method we must always define it.
   */
   @OutputProperty()
   witnessHostDiskMapping: VsanDiskMapping;

   /*
    The storagePoolDisks property is set in ClaimDisksWitnessHostPage.
    It is conditional based on whether the selected host is a Witness OVF virtual appliance and vsan max is enabled.
    Since the property is required for 'startingTaskProperty' method we must always define it.
    */
   @OutputProperty()
   storagePoolDisks: string[];

   @OutputProperty()
   isWitnessDeployedFromOvf: boolean = false;

   /*
    * Inject hostInventoryService as public as it is needed by the vsan-inventory-browser
    */
   constructor(public hostInventoryService: WitnessCandidateInventoryService,
         private hostValidationService: WitnessValidationService,
         private stretchedClusterService: VsanStretchedClusterService,
         private configureStretchedClusterService: ConfigureStretchedClusterService,
         private diskManagementService: DiskManagementService) {
   }

   async ngOnInit(): Promise<void> {
      this.inventoryRoot = [ManagedObject.vcRoot(this.clusterRef.serverGuid)];
      this.setInventoryFilterContext();
   }

   private setInventoryFilterContext(): void {
      if (this.currentWitnessHostRef) {
         const filterContext: Map<FilterContextKey, ManagedObject> = new Map();
         filterContext.set(FilterContextKey.CURRENT_WITNESS_HOST_REF, this.currentWitnessHostRef);
         this.filterContext = filterContext;
      }
   }

   async hostSelected(event: InventorySelectionEvent): Promise<void> {
      if (ManagedObject.areEqual(event.node.data.nodeRef, this.witnessHost)) {
         // Same host selected, nothing to do
         return;
      }
      this.resetValidation();
      this.updateSelection(event.node.data);
      if (!this.witnessHost) {
         // Not a valid item selected from inventory, nothing to do further
         return;
      }
      await this.queryAndValidateSelectedHost();
   }

   private resetValidation(): void {
      this.hostValidation = null;
      this.pageControls.clearValidationErrors();
   }

   private updateSelection(host: InventoryEntryData): void {
      this.hasDiskGroupsOnWitness = false;
      if (host.nodeRef.type === ManagedObjectUtils.HOST || host.nodeRef.type === ManagedObjectUtils.COMPUTE_RESOURCE) {
         this.witnessHost = host.nodeRef;
         this.witnessHostName = host.name;
      } else {
         this.witnessHost = null;
         this.witnessHostName = "";
      }
   }

   private async queryAndValidateSelectedHost(): Promise<void> {
      this.pageControls.setBusyState();

      [this.hostValidation, this.isWitnessDeployedFromOvf, this.hasDiskGroupsOnWitness] = await Promise.all([
         this.getHostValidationResult(),
         this.getIsWitnessDeployedFromOvf(),
         this.hasWitnessClaimedDisks(),
      ]);

      this.pageControls.clearBusyState();
      this.pageControls.rebuildToc();
   }

   private async getHostValidationResult(): Promise<WitnessHostValidation> {
      try {
         const hostValidationResult: WitnessHostValidationResult =
               await this.configureStretchedClusterService.getNewWitnessHostValidationError(
                     this.clusterRef,
                     this.witnessHost,
                     this.vsanConfigSpec.isVsanMaxEnabled);
         return this.hostValidationService.handleSingleClusterWitnessValidationResult(
               hostValidationResult, this.vsanConfigSpec);
      } catch (error) {
         Logger.error("Unable to get host validation result: " + error);
         return this.hostValidationService.handleHostValidationError(error);
      }
   }

   private async getIsWitnessDeployedFromOvf(): Promise<boolean> {
      try {
         return await this.stretchedClusterService.isWitnessDeployedFromOvf(this.witnessHost);
      } catch (error) {
         Logger.error("Unable to determine if the selected host is a Virtual appliance deployed from an OVF: ", error);
         return false;
      }
   }

   private async hasWitnessClaimedDisks(): Promise<boolean> {
      try {
         const witnessDisksData: VsanClaimDisksData = await this.diskManagementService
               .getHostDisksClaimingData(this.witnessHost, this.vsanConfigSpec.isVsanMaxEnabled);
         return this.vsanConfigSpec.isVsanMaxEnabled
               ? DiskMgmtUtil.hasVsanMaxClaimedDisks(witnessDisksData)
               : DiskMgmtUtil.hasDiskGroups(witnessDisksData);
      } catch (e) {
         // TODO: dtsvetkova - remove this once 2927115 is resolved
         Logger.error("Unable to get witness host's claimed disks: " + e);
         return false;
      }

   }

   validate(): string[] {
      if (this.hostValidation &&
            [AlertType.DANGER, AlertType.WARNING].includes(this.hostValidation.validationAlertType)) {
         // Prevent the user from proceeding if the selected host had validation errors.
         return this.hostValidation.validationMessages;
      }
      return null;
   }
}
