import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {DomainTreeItem} from "@component/wizard/shared-pages/single-site-fault-domains-page.component";

@Component({
   templateUrl: "delete-domain-warning.html",
   styleUrls: ["delete-domain-warning.scss"],
   selector: "vsan-delete-domain-warning",
})
export class DeleteDomainWarningComponent {

   @Input()
   domains: DomainTreeItem[] = [];

   private dialogVisible: boolean = false;

   @Input()
   set visible(value: boolean) {
      this.dialogVisible = value;
      this.visibleChange.emit(value);
   }

   get visible(): boolean {
      return this.dialogVisible;
   }

   @Output() visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();
   @Output() onConfirmation: EventEmitter<boolean> = new EventEmitter<boolean>();

   detailsExpanded: boolean = false;

   apply = (): void => {
      this.detailsExpanded = false;
      this.visible = false;
      this.onConfirmation.emit(true);
   };

   close = (): void => {
      this.detailsExpanded = false;
      this.visible = false;
      this.onConfirmation.emit(false);
   };

   get dialogTitle(): string {
      return this.domains.length > 1 ?
            VsanUiUtils.getString("vsan.faultDomain.emptyFaultDomainWarning.titleMulti", this.domains.length) :
            VsanUiUtils.getString("vsan.faultDomain.emptyFaultDomainWarning.titleSingle");
   }

   get warningDescription(): string {
      return this.domains.length > 1 ?
            VsanUiUtils.getString("vsan.faultDomain.emptyFaultDomainWarning.headerMulti", this.domains.length) :
            VsanUiUtils.getString("vsan.faultDomain.emptyFaultDomainWarning.headerSingle");
   }
}