import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanFaultDomainSpec} from "@generated/vsan-fault-domain-spec";
import {HostFaultDomainData} from "@generated/host-fault-domain-data";
import {FaultDomainsUtil} from "@util/fault-domains-util";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.faultDomains.title"),
      VsanUiUtils.getString("vsan.cluster.configure.wizard.faultDomains.description"),
)
@Component({
   templateUrl: "single-site-fault-domains-page.html",
   styleUrls: ["single-site-fault-domains-page.scss"],

})
export class SingleSiteFaultDomainsPage implements OnInit, OnValidate {
   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   clusterHostFaultDomainData: HostFaultDomainData[];

   @InputProperty()
   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   @OutputProperty()
   faultDomainNames: string[];

   @InputProperty()
   availableWitnessHostsCount: number;

   Icon = Icon;

   createDomainDialogVisible: boolean = false;

   failuresToTolerate: string = VsanUiUtils.getString("vsan.na.label");

   treeContent: TreeItem[] = [];

   private draggedItem: TreeItem;

   ngOnInit() {
      const domainChildren = {};
      const domains: DomainTreeItem[] = [];
      const standAloneHosts: HostTreeItem[] = [];

      for (const hostFaultDomainData of this.clusterHostFaultDomainData) {
         const hostUid = hostFaultDomainData.hostUid;

         let domainName = hostFaultDomainData.faultDomainName;
         if (domainName != null && domainName.length === 0) {
            domainName = null;
         }

         const hostItem: HostTreeItem = new HostTreeItem(
               ManagedObject.fromUid(hostUid),
               hostFaultDomainData.name,
               hostFaultDomainData.primaryIconId,
               domainName,
               false);

         if (!domainName) {
            standAloneHosts.push(hostItem);
         } else {
            const domainHosts: HostTreeItem[] =
                  domainChildren.hasOwnProperty(domainName) ? domainChildren[domainName] : [];
            domainHosts.push(hostItem);
            domainChildren[domainName] = domainHosts;
         }
      }

      for (const domainName of Object.keys(domainChildren)) {
         domains.push(new DomainTreeItem(domainName, Icon.FAULT_DOMAIN, false, domainChildren[domainName]));
      }

      this.treeContent.push(...domains);
      this.treeContent.push(...standAloneHosts);
      this.handleConfigChange();
   }

   private handleConfigChange = () => {
      this.clearSelection();
      this.sortTree();
      this.updateFttText();
   };

   private clearSelection = () => {
      this.treeContent.forEach((item): void => {
         item.selected = false;
         if (item instanceof DomainTreeItem) {
            item.hosts.forEach((host): void => {
               host.selected = false;
            });
         }
      });
   };

   private sortTree = (): void => {
      // sort tree root elements
      this.treeContent.sort((a: TreeItem, b: TreeItem): number => {
         if (a instanceof DomainTreeItem === b instanceof DomainTreeItem) {
            // comparing either domains or hosts
            return a.label > b.label ? 1 : -1;
         } else {
            // comparing host to a domain
            return a instanceof DomainTreeItem ? -1 : 1;
         }
      });

      // sort domain hosts
      this.treeContent.forEach((item) => {
         if (item instanceof DomainTreeItem && item.hosts) {
            item.hosts.sort((a: HostTreeItem, b: HostTreeItem): number => {
               return a.label > b.label ? 1 : -1;
            });
         }
      });
   };

   private updateFttText = (): void => {
      const domainAndStandaloneHostsCount: [number, number] = this.domainsAndStandaloneHostsCount;
      const domains: number = domainAndStandaloneHostsCount[0];
      const standaloneHosts: number = domainAndStandaloneHostsCount[1];

      this.failuresToTolerate = VsanUiUtils.getString("vsan.configure.faultDomains.faultsToTolerateText",
            FaultDomainsUtil.calculateFailuresToTolerate(standaloneHosts, domains, this.availableWitnessHostsCount));
   };

   private get domainsAndStandaloneHostsCount(): [number, number] {
      let domainsCount: number = 0;
      let standaloneHostsCount: number = 0;
      this.treeContent.forEach((item) => {
         if (item instanceof DomainTreeItem) {
            domainsCount++;
         } else if (item instanceof HostTreeItem) {
            standaloneHostsCount++;
         }
      });

      return [domainsCount, standaloneHostsCount];
   }

   selectItem = (itemName: string, event: any): void => {
      // if CTRL is not pressed select only current item
      if (!event.ctrlKey) {
         this.treeContent.forEach((item) => {
            item.selected = item.label === itemName;
            if (item instanceof DomainTreeItem) {
               item.hosts.forEach((host) => host.selected = host.label === itemName);
            }
         });
      }

      // if CTRL is pressed invert item selection and keep the rest of the selections
      if (event.ctrlKey) {
         this.treeContent.forEach((item) => {
            if (item.label === itemName) {
               item.selected = !item.selected;
               return;
            }

            if (item instanceof DomainTreeItem) {
               item.hosts.forEach((host) => {
                  if (host.label === itemName) {
                     host.selected = !host.selected;
                  }
               });
            }
         });
      }
   };

   onlyDomainsSelected = (): boolean => {
      let hostSelected: boolean = false;
      let domainSelected: boolean = false;
      this.treeContent.forEach((item) => {
         if (item instanceof DomainTreeItem) {
            if (item.selected) {
               domainSelected = true;
            }

            item.hosts.forEach((host) => {
               if (host.selected) {
                  hostSelected = true;
               }
            });
         } else {
            // host item
            if (item.selected) {
               hostSelected = true;
            }
         }
      });

      return domainSelected && !hostSelected;
   };

   openCreateDomainDialog = (): void => {
      this.createDomainDialogVisible = true;
   };

   deleteDomains = (): void => {
      // iterate root elements and if it is a selected domain remove it
      // and add its hosts to the end of the root elements list
      let index: number = this.treeContent.length;
      while (index--) {
         const item: TreeItem  = this.treeContent[index];
         if (item.selected && item instanceof DomainTreeItem) {
            item.hosts.forEach((host) => host.domainName = null);
            this.treeContent.push(...item.hosts);
            this.treeContent.splice(index, 1);
         }
      }

      this.handleConfigChange();
      this.pageControls.rebuildToc();
   };

   updateDomains = (newDomain: DomainTreeItem): void => {
      // Remove selected hosts out of the tree structure

      let index: number = this.treeContent.length;
      while (index--) {
         const item: TreeItem  = this.treeContent[index];
         if (item instanceof DomainTreeItem) {
            if (item.label !== newDomain.label && item.hosts.length !== 0) {
               const domainHosts: HostTreeItem[] = item.hosts;
               let hostsIndex: number = domainHosts.length;
               while (hostsIndex--) {
                  const domainHost: HostTreeItem  = domainHosts[hostsIndex];
                  if (newDomain.hosts.indexOf(domainHost) >= 0) {
                     domainHosts.splice(hostsIndex, 1);
                  }
               }
            }
         } else if (item instanceof HostTreeItem) {
            if (newDomain.hosts.indexOf(item) >= 0) {
               this.treeContent.splice(index, 1);
            }
         }
      }

      // delete empty fault domains
      let deleteIndex: number = this.treeContent.length;
      while (deleteIndex--) {
         const item: TreeItem  = this.treeContent[deleteIndex];
         if (item instanceof DomainTreeItem && item.hosts.length === 0) {
            this.treeContent.splice(deleteIndex, 1);
         }
      }

      if (newDomain.hosts.length > 0) {
         // add the new fault domain
         newDomain.hosts.forEach((i) => i.domainName = newDomain.label);

         /**
          * Search the new domain in the tree items,
          * if a new fault domain, we simply push it into the list, otherwise we do nothing.
          */
         if (!this.treeContent.find(item => item instanceof DomainTreeItem && item.label === newDomain.label)) {
            this.treeContent.push(newDomain);
         }
      }

      this.handleConfigChange();
      this.pageControls.rebuildToc();
   };

   validate = (): string[] => {
      this.faultDomainNames = [];
      this.vsanConfigSpec.faultDomainSpecs = [];

      // prepare fault domains specs and list of fault domain names
      for (const item of this.treeContent) {
         if (item instanceof DomainTreeItem) {
            const faultDomain: string = item.label;
            this.faultDomainNames.push(faultDomain);
            for (const host of item.hosts) {
               this.vsanConfigSpec.faultDomainSpecs.push(VsanFaultDomainSpec.Factory.create(faultDomain, host.hostRef));
            }
         } else if (item instanceof HostTreeItem) {
            this.vsanConfigSpec.faultDomainSpecs.push(VsanFaultDomainSpec.Factory.create("", item.hostRef));
         }
      }
      return null;
   };

   /**
    * Invoked when a host is dragged over a valid target
    * in order to hint the user they can drop it.
    * @param targetItem the potential drop target, can be fault domain,
    *       other host or null (for header and footer)
    */
   hintDropTarget = (targetItem?: TreeItem) => {
      if (!this.draggedItem || this.draggedItem instanceof DomainTreeItem) {
         return;
      }

      this.draggedItem.badgeText = "";

      let domain: DomainTreeItem = null;
      if (targetItem) {
         domain = targetItem instanceof DomainTreeItem ?
               targetItem : this.findDomainByHost(targetItem as HostTreeItem);
      }

      if (domain != null) {
         if (domain !== this.findDomainByHost(this.draggedItem as HostTreeItem)) {
            this.draggedItem.badgeText = VsanUiUtils.getString(
                  "vsan.faultDomains.action.moveHosts.hint.move", domain.label);
         }
      } else if (this.draggedItem instanceof HostTreeItem && this.draggedItem.domainName) {
         // Drag a host out of a domain
         domain = this.findDomainByHost(this.draggedItem);
         if (domain) {
            this.draggedItem.badgeText = VsanUiUtils.getString(
                  "vsan.faultDomains.action.moveHosts.hint.remove", domain.label);
         }
      }
   };

   private findDomainByHost = (host: HostTreeItem): DomainTreeItem => {
      if (!host) {
         return null;
      }
      const matched: TreeItem[] = this.treeContent.filter(
            item => item instanceof DomainTreeItem && item.hosts.indexOf(host) > -1);
      if (matched.length > 0) {
         return matched[0] as DomainTreeItem;
      }
      return null;
   };

   itemDragged = (item: TreeItem): void => {
      this.draggedItem = item instanceof HostTreeItem ? item : null;
   };

   clearDraggedItem = (): void => {
      if (this.draggedItem != null) {
         this.draggedItem.badgeText = null;
      }
      this.draggedItem = null;
   };

   itemDropped = (event, targetItem?: TreeItem): void => {
      if (!this.draggedItem) {
         return;
      }

      let domain: DomainTreeItem = null;
      if (targetItem) {
         domain = targetItem instanceof DomainTreeItem ?
               targetItem : this.findDomainByHost(targetItem as HostTreeItem);
      }

      if (domain != null && domain !== this.findDomainByHost(this.draggedItem as HostTreeItem)) {
         domain.hosts.push(this.draggedItem as HostTreeItem);
      } else if (domain == null) {
         // Drag a host out of a fault domain
         if (this.draggedItem instanceof HostTreeItem && this.draggedItem.domainName) {
            domain = this.findDomainByHost(this.draggedItem);
            domain.hosts.splice(domain.hosts.indexOf(this.draggedItem), 1);
            this.treeContent.push(this.draggedItem);
            this.draggedItem.domainName = null;
         }
      }
      this.updateDomains(domain);
      this.clearDraggedItem();
   };
}

export abstract class TreeItem {
   protected constructor(public label: string, public icon: string,
         public selected: boolean, public badgeText: string = null) {}
}

export class DomainTreeItem extends TreeItem {
   constructor(public label: string, public icon: string, public selected: boolean,
         public hosts: HostTreeItem[]) {
      super(label, icon, selected);
   }
}

export class HostTreeItem extends TreeItem {
   constructor(public hostRef: ManagedObject, public label: string, public icon: string,
         public domainName: string, public selected: boolean) {
      super(label, icon, selected);
   }
}
