import {Component, EventEmitter, Input, Output} from "@angular/core";
import {DomainOrHostData} from "@generated/domain-or-host-data";
/**
 * A separate component is required because of https://github.com/vmware/clarity/issues/5514
 */
@Component({
   selector: "vsan-fault-domain-nodes",
   templateUrl: "fault-domain-nodes.html",
   styleUrls: ["fault-domain-nodes.scss"],
})
export class FaultDomainNodesComponent {

   @Input()
   domain: DomainOrHostData;

   @Input()
   selectedItem: DomainOrHostData;

   @Input()
   dragScope: string;

   @Output()
   onSelect: EventEmitter<DomainOrHostData> = new EventEmitter<DomainOrHostData>();

   @Output()
   onDrop: EventEmitter<{dragData: DomainOrHostData}> = new EventEmitter<{dragData: DomainOrHostData}>();
}