import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {DiskData} from "@generated/disk-data";
import {PageItem} from "@component/wizard/page-item";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.claimDisksWitnessHost.title"),
      VsanUiUtils.getString("vsan.cluster.configure.wizard.claimDisksWitnessHost.description"),
)
@Component({
   templateUrl: "claim-disks-witness-host-page.html",
})
export class ClaimDisksWitnessHostPage implements OnInit, OnValidate {

   @InputProperty()
   pageControls: PageControls;

   @InputProperty(InputProperty.required(false))
   clusterRef: ManagedObject;

   @InputProperty()
   witnessHost: ManagedObject;

   @InputProperty()
   isVsanMaxWorkflow: boolean;

   @OutputProperty()
   witnessHostDiskMapping: VsanDiskMapping;

   @OutputProperty()
   storagePoolDisks: string[];

   private errors: string[];

   async ngOnInit(): Promise<void> {
      // Block the Next button while host's disks are loading
      (this.pageControls as PageItem).nextButtonIsDisabled = true;
   }

   onLoadingComplete(): void {
      (this.pageControls as PageItem).nextButtonIsDisabled = false;
   }

   configChange(diskMapping: VsanDiskMapping): void {
      this.witnessHostDiskMapping = diskMapping;
      this.pageControls.rebuildToc();
   }

   onStoragePoolDisksChange(storagePoolDisks: DiskData[]): void {
      if (VsanUiUtils.isEmpty(storagePoolDisks)) {
         this.storagePoolDisks = null;
         return;
      }
      this.errors = null;
      this.storagePoolDisks = storagePoolDisks.map(storagePoolDisk => storagePoolDisk.disk.canonicalName);
   }

   configValidate(errors: string[]): void {
      this.errors = errors;
   }

   validate(): string[] {
      if (this.isVsanMaxWorkflow) {
         if (VsanUiUtils.isEmpty(this.storagePoolDisks)) {
            this.errors = [VsanUiUtils.getString("vsan.dialog.addDisksToStoragePool.error.selectDisks")];
         } else {
            this.errors = null;
         }
      }
      return VsanUiUtils.isEmpty(this.errors) ? null : this.errors;
   }
}