import {PipeTransform, Pipe} from "@angular/core";
import {HostTreeItem} from "@component/wizard/shared-pages/single-site-fault-domains-page.component";

@Pipe({
   name: 'hostFilter'
})
export class HostFilterPipe implements PipeTransform {

   /**
    * Filters host items list.
    *
    * @param items                     list to be filtered
    * @param showOnlyStandaloneHosts   if only standalone hosts has to be left (standalone hosts are the ones that
    *                                  are not part of a domain)
    * @returns {HostTreeItem[]}        filtered list
    */
   transform(items: HostTreeItem[],  showOnlyStandaloneHosts: boolean): any {
      return showOnlyStandaloneHosts ? items.filter((item: HostTreeItem) => !item.domainName) : items;
   }
}