import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanUiUtils} from "@util/vsan-util";
import {FaultDomainsUtil} from "@util/fault-domains-util";

@Component({
   templateUrl: "fault-domains-page.html",
   styleUrls: ["fault-domains-page.scss"],
})
@WizardPage(
      VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.title"),
      VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.description"),
)
export class FaultDomainsPageComponent implements OnInit, OnValidate {

   static readonly DEFAULT_PREFERRED_NAME: string = VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.defaultPreferredName");
   static readonly DEFAULT_SECONDARY_NAME: string = VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.defaultSecondaryName");

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   availableDomains: DomainOrHostData[] = [];

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.noName")))
   preferredName: string = FaultDomainsPageComponent.DEFAULT_PREFERRED_NAME;

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.noHosts")))
   preferredDomains: DomainOrHostData[] = [];

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.noName")))
   secondaryName: string = FaultDomainsPageComponent.DEFAULT_SECONDARY_NAME;

   @OutputProperty(OutputProperty.ifEmpty(VsanUiUtils.getString(
         "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.noHosts")))
   secondaryDomains: DomainOrHostData[] = [];

   @InputProperty(InputProperty.required(false))
   hciWorkflow: boolean = false;

   ngOnInit() {
      const preferredDomain: DomainOrHostData = this.availableDomains.find(domain => domain.isPreferred);

      if (preferredDomain) {
         // This is the "HCI extend" workflow. We have preferred and secondary fault domains already set.
         // Put the preferred fault domain and the standalone hosts into the first column.
         // Others should be in the second one.
         this.preferredDomains.push(...this.availableDomains.filter(domain => domain.isPreferred || domain.isHost));
         this.secondaryDomains.push(...this.availableDomains.filter((domain) => !domain.isPreferred && !domain.isHost));
      } else {
         // This is the "configure Stretched Cluster" workflow and we don't have preferred fault domain set yet.
         // Put everything in the "preferred" section and let the user organize them
         this.preferredDomains.push(...this.availableDomains);
      }

      const actualDomains: DomainOrHostData[] = this.preferredDomains.filter(i => !i.isHost);
      this.preferredName = actualDomains.length === 1
            ? actualDomains[0].label
            : FaultDomainsPageComponent.DEFAULT_PREFERRED_NAME;
      this.secondaryName = FaultDomainsPageComponent.DEFAULT_SECONDARY_NAME;
   }

   selectedPreferredItem: DomainOrHostData;
   selectedSecondaryItem: DomainOrHostData;

   selectPreferred = (value: DomainOrHostData) => this.selectedPreferredItem = value;
   selectSecondary = (value: DomainOrHostData) => this.selectedSecondaryItem = value;
   moveToPreferred = () => this.fromSecondaryToPreferred(this.selectedSecondaryItem);
   moveToSecondary = () => this.fromPreferredToSecondary(this.selectedPreferredItem);
   dropToPreferred = (event) => this.fromSecondaryToPreferred(event.dragData);
   dropToSecondary = (event) => this.fromPreferredToSecondary(event.dragData);

   private fromPreferredToSecondary(data: DomainOrHostData) {
      FaultDomainsUtil.move(data, this.preferredDomains, this.secondaryDomains);
      this.selectedPreferredItem = null;
      this.pageControls.rebuildToc();
   }

   private fromSecondaryToPreferred(data: DomainOrHostData) {
      FaultDomainsUtil.move(data, this.secondaryDomains, this.preferredDomains);
      this.selectedSecondaryItem = null;
      this.pageControls.rebuildToc();
   }

   validate(): string[] {
      if (!this.hciWorkflow &&
            (!this.hasConnectedHosts(this.preferredDomains) || !this.hasConnectedHosts(this.secondaryDomains))) {
         return [VsanUiUtils.getString(
               "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.noHosts")];
      }
      if (this.preferredName.trim() === this.secondaryName.trim()) {
         return [VsanUiUtils.getString(
               "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.validation.duplicateName")];
      }
      return null;
   }

   /**
    * Returns true if the list of domains and hosts contain at least one connected host.
    */
   hasConnectedHosts(domainsAndHosts: DomainOrHostData[]): boolean {
      const allHosts: DomainOrHostData[] = [];
      for (const item of domainsAndHosts) {
         if (item.isHost) {
            allHosts.push(item);
         } else {
            allHosts.push(...item.children);
         }
      }
      const firstConnectedHost = allHosts.find(host => !host.inMaintenanceMode);
      return !!firstConnectedHost;
   }
}
