import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {DomainTreeItem, HostTreeItem, TreeItem} from "./single-site-fault-domains-page.component";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn
} from "@angular/forms";
import {ReactiveFormUtil} from "@util/reactive-form-util";

@Component({
   templateUrl: "create-fault-domain.html",
   styleUrls: ["create-fault-domain.scss"],
   selector: "vsan-create-fault-domain"
})
export class CreateFaultDomainComponent {

   @Input()
   domainsAndHosts: TreeItem[];

   readonly DOMAIN_NAME: string = "domainName";
   readonly VALIDATION_ERROR: string = "validationError";

   createFaultDomain: FormGroup;

   invalidDomainNameMessage: string;

   private _visible: boolean;

   // Used to track if the dialog was modified or not
   private initialState: boolean = true;

   constructor(private formBuilder: FormBuilder) {
      this.createFaultDomain = this.formBuilder.group({
         [this.DOMAIN_NAME]:
               new FormControl("", [this.validateDomainName])
      });
   }

   @Input()
   set visible(value: boolean) {
      this._visible = value;
      this.visibleChange.emit(value);
      if (value) {
         this.resetDialog();
         this.initData();
      }
   }

   get visible(): boolean {
      return this._visible;
   }

   @Output()
   visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   @Output()
   onConfirmation: EventEmitter<DomainTreeItem> = new EventEmitter<DomainTreeItem>();

   alertMessages: string[] = [];
   showOnlyStandaloneHosts: boolean = true;
   deleteDomainWarningVisible: boolean = false;

   selectedHosts: HostTreeItem[] = [];
   allHosts: HostTreeItem[] = [];

   resetDialog = (): void => {
      this.showOnlyStandaloneHosts = true;
      this.allHosts = [];
      this.selectedHosts = [];
      this.initialState = true;
      this.createFaultDomain.markAsUntouched();
      this.clearValidation();
   };

   initData = (): void => {
      if (this.domainsAndHosts) {
         this.domainsAndHosts.forEach((item: TreeItem): void => {
            if (item instanceof DomainTreeItem) {
               if (item.hosts) {
                  item.hosts.forEach((host: HostTreeItem): void => {
                     this.allHosts.push(host);
                     if (host.selected || item.selected) {
                        this.selectedHosts.push(host);

                        // if a host in a fault domain is selected
                        // show all host in the datagrid
                        this.showOnlyStandaloneHosts = false;
                     }
                  });
               }
            } else if (item instanceof HostTreeItem) {
               this.allHosts.push(item);
               if (item.selected) {
                  this.selectedHosts.push(item);
               }
            }
         });
      }
   };

   apply = (): void => {
      if (!this.isValid()) {
         return;
      }

      if (this.domainsForDelete.length > 0) {
         this.deleteDomainWarningVisible = true;
      } else {
         this.createDomain();
         this.close();
      }
   };

   isValid(): boolean {
      this.initialState = false;
      if (!this.createFaultDomain.valid) {
         // If form is not touched, validation should be triggered manually in order to show validation errors
         if (!this.createFaultDomain.touched) {
            ReactiveFormUtil.validateAllFormFields(this.createFaultDomain);
         }

         return false;
      }

      if (this.selectedHosts.length === 0) {
         this.alertMessages.push(VsanUiUtils.getString("vsan.faultDomains.action.createDomain.validation.noHost"));
         return false;
      }
      return true;
   }

   get isApplyButtonBlocked(): boolean {
      return this.createFaultDomain.touched && this.createFaultDomain.invalid;
   }

   validateDomainName: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      this.invalidDomainNameMessage = this.getDomainNameErrorMessage(control.value.trim());
      if (!this.invalidDomainNameMessage) {
         return null;
      }

      return {
         [this.VALIDATION_ERROR]: {
            value: this.invalidDomainNameMessage
         }
      };
   };

   getDomainNameErrorMessage(name: string): string {
      if (!name) {
         return VsanUiUtils.getString("vsan.faultDomains.action.createDomain.validation.noName");
      }  else if (name.length > 256) {
         return VsanUiUtils.getString("vsan.faultDomains.action.renameDomain.validation.nameTooLong");
      } else if (this.getExistingDomainNames().indexOf(name) >= 0) {
         return VsanUiUtils.getString("vsan.faultDomains.action.createDomain.validation.duplicateName");
      }

      return null;
   }

   clearValidation = (): void => {
      this.alertMessages = [];
      this.invalidDomainNameMessage = null;
   };

   createDomain = (): void => {
      const domainName = this.createFaultDomain.get(this.DOMAIN_NAME).value.trim();
      this.createFaultDomain.get(this.DOMAIN_NAME).patchValue("");

      this.onConfirmation.emit(
            new DomainTreeItem(domainName, Icon.FAULT_DOMAIN,
                  false, this.selectedHosts));
   };

   close = (): void => {
      this.visible = false;
   };

   applyFilter = (onlyStandalone: boolean): void => {
      this.showOnlyStandaloneHosts = onlyStandalone;
      // clear selected host if the datagrid perspective changes
      this.selectedHosts = [];
   };

   getExistingDomainNames = (): string[] => {
      const result: string[] = [];
      this.domainsAndHosts.forEach((item): void => {
         if (item instanceof DomainTreeItem) {
            result.push(item.label);
         }
      });

      return result;
   };

   get domainsForDelete(): DomainTreeItem[] {
      const result: DomainTreeItem[] = [];
      this.domainsAndHosts.forEach((item): void => {
         if (item instanceof DomainTreeItem && item.hosts) {
            // check which of the hosts under the domain are part of the selected hosts list
            const hostHasToBeDeleted: boolean = item.hosts.every((host) => {
               return this.selectedHosts.indexOf(host) !== -1;
            });

            if (hostHasToBeDeleted) {
               result.push(item);
            }
         }
      });

      return result;
   }

   get createDomainDialogClass(): string {
      return this.deleteDomainWarningVisible ? "disable-action" : "";
   }

   /**
    * Handles user choice if a new domain has to be created or not.
    * This is invoked only if the domain's host were part of domains
    * that will be left empty. As there is no sense to keep empty domains,
    * all empty domains will be deleted.
    *
    * @param deleteEmptyDomains    if the user confirmed that the new domains has to be deleted
    *                              and all previously created domains without host will be wiped out
    */
   onCreateNewDomainConfirmation = (deleteEmptyDomains: boolean): void => {
      if (deleteEmptyDomains) {
         this.createDomain();
         this.close();
      }

      this.deleteDomainWarningVisible = false;
   };
}
