import {Component, EventEmitter, Input, Output} from "@angular/core";
import {TreeItem} from "@component/wizard/shared-pages/single-site-fault-domains-page.component";
/**
 * A separate component is required because of https://github.com/vmware/clarity/issues/5514
 */
@Component({
   selector: "vsan-single-site-fault-domain-nodes",
   templateUrl: "single-site-fault-domain-nodes.html",
   styleUrls: ["single-site-fault-domain-nodes.scss"],
})
export class SingleSiteFaultDomainNodesComponent {
   @Input()
   rootItem: FaultDomainItem;

   @Output()
   onSelectItem: EventEmitter<{label: string, event: any}> = new EventEmitter<{label: string, event: any}>();

   @Output()
   onDragEnter: EventEmitter<any> = new EventEmitter<any>();

   @Output()
   onDragStart: EventEmitter<any> = new EventEmitter<any>();

   @Output()
   onDrop: EventEmitter<{event: any, host: any}> = new EventEmitter<{event: any, host: any}>();
}

export class FaultDomainItem extends TreeItem {
   hosts: any;
}